import React from 'react';
import s from './ProfileInto.module.css';
import Preloader from '../../common/Preloader/Preloader';
import nullAvatar from '../../../assets/images/nullAvatar.jpg';
import ProfileStatusWithHocks from './ProfileStatusWithHocks';

const ProfileInto = ({ profile, status, updateStatus }) => {
	if (!profile) {
		return <Preloader />
	};
	return (
		<>
			<div className={s.profile_info}>
				<img src={profile.photos.large != null ? profile.photos.large : nullAvatar} width='15%' height='5%' />
				<div className={s.info}>
					<h2>{profile.fullName}</h2>
					<h3>{profile.lookingForAJobDescription}</h3>
					<p>{profile.aboutMe}</p>
					<ul>
						<li>{profile.contacts.facebook != null ? profile.contacts.facebook : 'null'}</li>
						<li>{profile.contacts.website != null ? profile.contacts.website : 'null'}</li>
						<li>{profile.contacts.vk != null ? profile.contacts.vk : 'null'}</li>
						<li>{profile.contacts.twitter != null ? profile.contacts.twitter : 'null'}</li>
						<li>{profile.contacts.instagram != null ? profile.contacts.instagram : 'null'}</li>
						<li>{profile.contacts.youtube != null ? profile.contacts.youtube : 'null'}</li>
						<li>{profile.contacts.github != null ? profile.contacts.github : 'null'}</li>
						<li>{profile.contacts.mainLink != null ? profile.contacts.mainLink : 'null'}</li>
					</ul>
				</div>
			</div>
			<div className={s.profileStatus} >
				<ProfileStatusWithHocks status={status} updateStatus={updateStatus} />
			</div>
		</>

	);
};

export default ProfileInto;