import React from 'react';
import s from './MyPosts.module.css';

const MyPosts = (props) => {
	return(
		<div className={s.info}>
			<img src='https://i.pinimg.com/736x/4a/bc/c0/4abcc00427dbb86ee5da8270b52204f8.jpg' width='5%' height='5%'/>
			<div className={s.info_text}>
				<h4>{props.nick}</h4>
                <p>{props.post} {props.likecount} likes</p>
			</div>
		</div>
	);
};

export default MyPosts;